#work{
    min-height: 285px;
    padding: 1rem 1rem ;
    /* box-shadow: 0px 12px 36.8px 9.2px rgba(0, 0, 0, 0.1); */
    border-radius: 5px !important;
    animation-duration: 2s;
    animation-fill-mode: both;
    -webkit-animation-duration: 2s;
    -webkit-animation-fill-mode: both;
    opacity: 0;
    animation-name: fadeInUp;
    -webkit-animation-name: fadeInUp;
    margin-top: -180px;
}


@keyframes fadeInUp {
    from {
        transform: translate3d(0,40px,0)
    }

    to {
        transform: translate3d(0,0,0);
        opacity: 1
    }
}
#work .button-group{
    display: flex;
    justify-content: space-between;
}

.button-group button{
    position: relative;
    padding: 5px 30px;
    border: 1px solid white;
    font-weight: bold;
    color: white;
    background-color: inherit;
    outline: none;
    transition: all .35s;
    cursor: pointer;
    font-family: sans-serif;
    font-size: 16px;
}

.button-group .or{
    display: none;
    position: absolute;
    background-color: #fff;
    width: 26px;
    height: 26px;
    line-height: 26px;
    font-style: italic;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 11.2px;
    font-weight: bold;
    text-align: center;
    color: #666;
    border: 1px  solid #ddd;
    border-radius: 100%;
    top: 50%;
    margin-top: -13px;
    margin-right: -10px;
}

.button-group button span{
    position: relative;
    z-index: 2;
}

.button-group button:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: orange;
    transition: all .35s linear;
}

.button-group button:hover{
    color: #000;
}

.button-group button:hover:before{
    width: 100%;
}


audio  input[pseudo="-webkit-media-controls-play-button"] {
    width: 5rem !important;
    background-color: red !important;
}


#work .btn-play-pause{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

#work .btn-play-pause .svg-inline--fa{
    font-size: 3.35rem;
    opacity: .8;
    cursor: pointer;
}

#work .btn-play-pause .svg-inline--fa:hover{
    color: #999999;
}


#work p{
    margin-bottom: 0 !important;
}

#work .info span span{
    color: #a5c261;
}

#work > .info > .work-info-text > span{
    display:block;
    font-size: 13px;
    font-family: monospace;
    line-height: 1.2;
    color: #e6e1dc;
    font-weight: 300;
}

#work > .info{
    position: relative;
    background-color: #232323;
    color: white;
    padding: 10px 10px;
    border-radius: 4px;
    margin-top: 1rem;
    margin-bottom: 2.5rem;
}


