    border-radius: 10px;
    box-shadow: 0 6px 20px 3px rgba(0, 0, 0, 0.25);
    height: 147px;
    margin-top: 30px;
    width: 100%;
    transition: all .3s;
    transform-origin: 0% 0%;
    overflow-y: scroll;
    padding: 10px;
    font-size: 13px;
}

.job-summary{
    overflow-y: scroll;
    transition: all .3s;
    transform-origin: 0% 0%;
    height: 120px;
    margin-top: 30px;
    width: 100%;
}


::-webkit-scrollbar {
    width: 5px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
